<template>
  <div id="app" style="margin: 0 !important;">
    <v-app id="earn-learn-baccc">
      <v-main>
        <div class="fill-height d-flex flex-column justify-center">
          <div style="width: 100%">
            <div
              style="width: 100%"
              class="d-flex align-center justify-center ma-3 ma-sm-4 ma-md-5 ma-lg-6 ma-xl-7"
            >
              <img style="width: 3.5vmax; height: 3.5vmax;" src="./assets/logo.png">
              <p
                class="page-title pa-0 my-0 mx-4 font-weight-bold raleway"
                style="color: #167998"
              >
                EARN & LEARN
              </p>
            </div>
            <v-row align="center" justify="center">
              <div :style="mapSize">
                <district-map :onFocus="onFocus" :onSelect="() => {}" ref="dMap"></district-map>
              </div>
              <div
                class="d-flex flex-column ma-3 ma-sm-4 ma-md-5 ma-lg-6 ma-xl-7"
                :style="searchSize"
              >
                <div style="width: 100%" class="d-flex">
                  <v-btn icon color="grey" class="mx-2 mt-2">
                    <v-icon
                      v-if="showIndustries"
                      @click="() => { showIndustries = false; }"
                    >
                      mdi-chevron-up
                    </v-icon>
                    <v-icon
                      v-if="!showIndustries"
                      @click="() => { showIndustries = true; }"
                    >
                      mdi-chevron-down
                    </v-icon>
                  </v-btn>
                  <v-text-field
                    v-model="query"
                    :solo="true"
                    :clearable="true"
                    label="Search for employers..."
                    type="text"
                    :loading="searching ? 'cyan darken-3' : false"
                    :error-messages="failed ? 'Unable to find employers!' : undefined"
                    class="flex-grow-1 raleway"
                    @keydown.enter="search"
                  >
                  </v-text-field>
                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        v-bind="attrs"
                        v-on="on"
                        icon
                        @click="search"
                        color="cyan darken-3"
                        class="mx-2 mt-2"
                      >
                        <v-icon>mdi-account-search-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>Search For Employers</span>
                  </v-tooltip>
                </div>
                <v-fade-transition>
                  <v-autocomplete
                    v-model="selectedIndustries"
                    v-if="showIndustries"
                    :items="industries"
                    chips
                    label="Select Industries..."
                    multiple
                    solo
                    class="raleway"
                    color="cyan darken-3"
                    item-color="cyan darken-3"
                  >
                    <template v-slot:selection="data">
                      <v-chip
                        label
                        text-color="white"
                        color="cyan darken-3"
                        v-bind="data.attrs"
                        class="raleway"
                        :input-value="data.selected"
                        close
                        @click="data.select"
                        @click:close="remove(data.item)"
                      >
                        <v-tooltip bottom>
                          <template v-slot:activator="{ on, attrs }">
                            <span
                              v-bind="attrs"
                              v-on="on"
                              class="d-inline-block text-truncate"
                              style="max-width: 15ch;"
                            >
                              {{ data.item }}
                            </span>
                          </template>
                          <span>{{ data.item }}</span>
                        </v-tooltip>
                      </v-chip>
                    </template>
                    <template v-slot:item="data">
                      <template>
                        <v-list-item-content>
                          <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                              <span
                                class="raleway d-inline-block text-truncate text-left"
                                style="max-width: 40ch; height: 2ch;"
                                v-bind="attrs"
                                v-on="on"
                              >
                                {{ data.item }}
                              </span>
                            </template>
                            <span>{{ data.item }}</span>
                          </v-tooltip>
                        </v-list-item-content>
                      </template>
                    </template>
                  </v-autocomplete>
                </v-fade-transition>
                <v-fade-transition>
                  <virtual-list
                    class='no-scrollbar flex-grow-1'
                    style='overflow-y: auto; width: 100%'
                    data-key='id'
                    :data-sources='results'
                    :data-component='itemComponent'
                    :estimate-size="120"
                    :extra-props="{ numItems: results.length }"
                    v-if="searched"
                  />
                </v-fade-transition>
                <div
                  class='flex-grow-1 flex-shrink-0'
                  style='overflow-y: auto; width: 100%'
                  v-if="!searched"
                />
              </div>
            </v-row>
          </div>
        </div>
      </v-main>
    </v-app>
    <div class="district-tooltip" v-show="tooltipActive" :style="tooltipPos">
      {{tooltip}}
    </div>
  </div>
</template>

<script>
import VirtualList from 'vue-virtual-scroll-list';
import Fuse from 'fuse.js';
import search from './backend';
import DistrictMap from './components/Map.vue';
import EmployerItem from './components/EmployerItem.vue';

export default {
  name: 'App',
  data: () => ({
    tooltip: '',
    tooltipPos: '',
    tooltipActive: false,
    districts: [
      'NORTH MONTEREY COUNTY',
      'PACIFIC GROVE UNIFIED',
      'MONTEREY PENINSULA UNIFIED',
      'SALINAS UNION HIGH',
      'CARMEL UNIFIED',
      'GONZALES UNIFIED',
      'SOLEDAD UNIFIED',
      'GREENFIELD UNION',
      'KING CITY UNION',
      'BITTERWATER-TULLY',
      'PACIFIC UNIFIED',
      'SAN ANTONIO UNION',
      'SAN LUCAS UNION',
      'SAN ARDO UNION',
      'BRADLEY UNION',
    ],
    industries: [
      'Agriculture, Forestry, Fishing and Hunting',
      'Mining',
      'Utilities',
      'Construction',
      'Manufacturing',
      'Wholesale Trade',
      'Retail Trade',
      'Transportation and Warehousing',
      'Information',
      'Finance and Insurance',
      'Real Estate Rental and Leasing',
      'Professional, Scientific, and Technical Services',
      'Management of Companies and Enterprises',
      'Administrative and Support and Waste Management and Remediation Services',
      'Educational Services',
      'Health Cate and Social Assistance',
      'Arts, Entertainment, and Recreation',
      'Accommodation and Food Services',
      'Other Services (except Public Administration)',
      'Public Administration',
      'Nonclassified',
    ],
    selectedIndustries: [],
    query: '',
    results: [],
    searching: false,
    searched: false,
    failed: false,
    showIndustries: false,
    itemComponent: EmployerItem,
  }),
  components: {
    DistrictMap,
    VirtualList,
  },
  computed: {
    mapSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return 'width: 70vmin; height: 70vmin;';
        case 'sm': return 'width: 65vmin; height: 65vmin;';
        case 'md': return 'width: 60vmin; height: 60vmin;';
        case 'lg': return 'width: 55vmin; height: 55vmin;';
        case 'xl': return 'width: 50vmin; height: 50vmin;';
        default: return 'width: 85vmin; height: 85vmin;';
      }
    },
    searchSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return 'width: 70vmin; height: 25vmin;';
        case 'sm': return 'width: 65vmin; height: 30vmin;';
        case 'md': return 'width: 60vmin; height: 35vmin;';
        case 'lg': return 'width: 45vmin; height: 55vmin;';
        case 'xl': return 'width: 45vmin; height: 50vmin;';
        default: return 'width: 45vmin; height: 85vmin;';
      }
    },
  },
  methods: {
    onFocus(id, x, y) {
      if (id === -1) {
        this.tooltipActive = false;
        return;
      }

      this.tooltip = this.districts[id];
      this.tooltipActive = true;
      this.tooltipPos = `top: ${y}px; left: ${x}px;`;
    },

    search() {
      this.results = [];
      this.searching = true;
      this.searched = false;
      this.showIndustries = false;

      search(
        this.$refs.dMap.getSelected(),
        this.selectedIndustries,
        (employers) => {
          this.failed = false;
          this.searched = true;
          this.searching = false;
          let results = [];
          if (this.query && this.query.trim()) {
            results = (
              (new Fuse(employers, { keys: ['employerName', 'subIndustry', 'address', 'city', 'employerAddress', 'telephone', 'naicis'] }))
                .search(this.query.trim())
                .map((v, index) => ({ index, ...v.item }))
            );
          } else results = employers;
          this.results = results;
        },
        () => {
          this.searching = false;
          this.failed = true;
        },
      );
    },

    remove(item) {
      const index = this.selectedIndustries.indexOf(item);
      if (index >= 0) this.selectedIndustries.splice(index, 1);
    },
  },
};
</script>

<style>
html { overflow-y: hidden !important; }

#app {
  font-family: Raleway, Helvetica, Arial, sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.raleway {
  font-family: Raleway, Helvetica, Arial, sans-serif !important;
}

.page-title {
  font-size: calc(14px + 3vmax);
}

.page-text {
  font-size: calc(12px + 1vmin);
}

.page-sub-text {
  font-size: calc(10px + 0.75vmin);
}

.district-tooltip {
  position: absolute;
  background-color: #115d74aa;
  color: white;
  font-size: 1em;
  padding: 0.25em;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.no-scrollbar {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
</style>
