const app = firebase.initializeApp(config);
const db = firebase.firestore(app);

const districts = [
  ['Moss Landing', 'Prunedale', 'Castroville', 'Marina', 'Oak Hills', 'Neponset', 'Blanco'],
  ['Pacific Grove', 'Del Monte Forest', 'Monterey'],
  ['Monterey', 'Seaside', 'Marina', 'Del Rey Oaks', 'Carmel-By-The-Sea', 'Hacienda Carmel', 'Rancho Tierra Grande', 'Corral De Tierra'],
  ['Boronda', 'Salinas', 'Spreckles', 'Corral De Tierra', 'Springtown', 'Bolas Knolls', 'Blanco'],
  ['Carmel-By-The-Sea', 'Carmel Highlands', 'Notleys Landing', 'Carmel Valley', 'Hacienda Carmel', 'Rancho Tierra Grande', 'Jamesburg'],
  ['Chualar', 'Penvir', 'Dean', 'Gonzales', 'Molus'],
  ['Molus', 'Camphora', 'Soledad', 'Fort Romie', 'Harlem'],
  ['Millers Ranch', 'Sycamore Flat', 'Greenfield', 'Metz'],
  ['Harlem', 'Metz', 'Elsa', 'King City'],
  ['Bitterwatter', 'Lonoak'],
  ['Lucia', 'Plaskett', 'Gorda'],
  ['Lockwood', 'Jolon'],
  ['San Lucas', 'Lonoak'],
  ['San Andro', 'Wunpost'],
  ['Bradley', 'Valleton', 'Nacimiento', 'San Miguel', 'Wunpost'],
];

export default function search(ids, industries, callback, fail) {
  const employers = db.collection('employer');
  const cities = [];
  for (let i = 0; i < ids.length; i += 1) {
    const district = districts[ids[i]];
    for (let j = 0; j < district.length; j += 1) {
      if (!cities.includes(district[j])) {
        cities.push(district[j]);
      }
    }
  }

  const requests = [];
  const cityRequests = [];
  for (let i = 0; i < cities.length; i += 10) {
    cityRequests.push(employers.where('city', 'in', cities.slice(i, i + 10)));
  }

  if (cityRequests.length === 0) {
    for (let i = 0; i < industries.length; i += 10) {
      requests.push(employers.where('industry', 'in', industries.slice(i, i + 10)));
    }
  } else {
    for (let i = 0; i < cityRequests.length; i += 1) {
      for (let j = 0; j < industries.length; j += 1) {
        requests.push(cityRequests[i].where('industry', '==', industries[i]));
      }
    }
  }

  if (requests.length === 0) {
    employers.get().then((snapshot) => {
      const data = [];

      snapshot.forEach((doc) => {
        data.push({
          id: doc.id,
          ...doc.data(),
        });
      });

      if (data.length > 0) callback(data);
      else fail();
    }).catch(() => fail());
    return;
  }

  const sentRequests = requests.map((r) => (
    r.get().then((snapshot) => {
      const data = [];

      snapshot.forEach((doc, index) => {
        data.push({
          index,
          id: doc.id,
          ...doc.data(),
        });
      });

      return data;
    })
  ));

  Promise.allSettled(sentRequests).then((results) => {
    let data = [];

    results.forEach((r) => {
      if (r.status === 'fulfilled') data = data.concat(r.value);
    });

    for (let i = 0; i < data.length; i += 1) {
      for (let j = i + 1; j < data.length; j += 1) {
        if (data[i].id === data[j].id) {
          data.splice(j, 1);
          j -= 1;
        }
      }
    }

    // eslint-disable-next-line no-param-reassign
    data.forEach((d, i) => { d.index = i; });
    if (data.length > 0) callback(data);
    else fail();
  });
}
